* {
  margin: 0;
  box-sizing: border-box;
}

.stars {
  display: flex;
}

.stars-star {
  background: url(images/star.svg) center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 :first-child,
.stars--2 :nth-child(-n+2),
.stars--3 :nth-child(-n+3),
.stars--4 :nth-child(-n+4),
.stars--5 :nth-child(-n+5) {
  background-image: url(images/star-active.svg);
}
